// Temp
.d .site-header:after {
  position: fixed;
  bottom: .5rem;
  left: .5rem;
  z-index: 100;
  color: $white;
  background: $secondary-color;
  border-radius: 100px;
  width: 3rem;
  height: 3rem;
  text-align: center;
  line-height: 3rem;
  @include breakpoint(small only) {
    content: "S";
  }
  @include breakpoint(medium only) {
    content: "M"; 
  }
  @include breakpoint(large only) {
    content: "L";
  }
  @include breakpoint(xlarge only) {
    content: "XL";
  }
  @include breakpoint(xxlarge only) {
    content: "XXL";
  }
}


body {overflow-x: hidden;}

.site {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  .site-content {
    flex: 1;
  }
}

.site-content {
  padding-top: 2rem;
}